
export { series, addChartOptions }

// assemble charts

function addChartOptions(pcd, settings) {
  pcd.chartOptions = chartOptions(pcd, settings)
  return pcd
}

function chartOptions(pcd, settings) {
  const length = pcd.means.length
  let categories
  if (pcd.positions == "between")
    categories = [...Array(length).keys()].map(i => `${i + 1}-${i + 2}`)
  else
    categories = [...Array(length).keys()].map(i => i + 1)

  return {
    chart: {
      // zoomType: "xy"
      height: settings.chartHeight,
      width: 600
    },
    legend: {
      enabled: false
    },
    title: null,
    xAxis: [
      {
        categories: categories,
        crosshair: true
      }
    ],
    yAxis: Y_AXIS_LIST,
    tooltip: { shared: true, valueDecimals: 4 },
    series: activeSeriesList(pcd, settings)
  }
}

function activeSeriesList(pcd, settings) {
  return Object.keys(series).filter(key => settings.selectedCharts.some(str => str == key)).map(key => series[key](pcd))
}

// y axis

const Y_AXIS_LIST = [
  {
    gridLineWidth: 0,
    title: null,
    labels: {
      format: "{value} bits",
      style: {
        color: "blue"
      }
    },
    opposite: true,
    min: 0,
    max: 1
  },
  {
    title: "value"
  },
  {
    // gridLineWidth: 0,
    title: "deviation",
    labels: {
      enabled: false
    }
  }
]

const Y_SIGNIFICANCE = 0
const Y_VALUE = 1
const Y_DEVIATION = 2

// possible series options

const seriesDefaults = {
    animation: false
}

const series = {
  significanceBars(pcd) {
    return {
      name: "significance",
      type: "column",
      yAxis: Y_SIGNIFICANCE,
      data: pcd.significances,
      tooltip: {
        valueSuffix: " ~"
      },
      ...seriesDefaults
    }
  },

  arearange(pcd) {
    return {
      name: "arearange",
      type: "arearange",
      yAxis: Y_VALUE,
      data: [...pcd.means.keys()].map(i => [pcd.means[i] + pcd.deviations[i], pcd.means[i] - pcd.deviations[i]]),
      marker: {
        enabled: false
      },
      ...seriesDefaults
    }
  },

  curvedMeanLine(pcd) {
    return {
      name: "mean",
      type: "spline",
      yAxis: Y_VALUE,
      data: pcd.means,
      marker: {
        enabled: true
      },
      tooltip: {
        valueSuffix: ""
      },
      ...seriesDefaults
    }
  },

  meanLine(pcd) {
    return {
      name: "mean",
      type: "line",
      yAxis: Y_VALUE,
      data: pcd.means,
      marker: {
        enabled: false
      },
      tooltip: {
        valueSuffix: ""
      },
      ...seriesDefaults
    }
  },

  meanErrorbar(pcd) {
    return {
      name: "errorbar",
      type: "errorbar",
      yAxis: Y_VALUE,
      data: [...pcd.means.keys()].map(i => [pcd.means[i] + pcd.deviations[i], pcd.means[i] - pcd.deviations[i]]),
      ...seriesDefaults
    }
  },

  deviationLine(pcd) {
    return {
      name: "deviation",
      type: "spline",
      yAxis: Y_DEVIATION,
      data: pcd.deviations,
      dashStyle: "shortdot",
      ...seriesDefaults
    }
  },

  columnrange(pcd) {
    return {
      name: "columnrange",
      type: "columnrange",
      yAxis: Y_VALUE,
      data: [...pcd.means.keys()].map(i => [pcd.means[i] + pcd.deviations[i], pcd.means[i] - pcd.deviations[i]]),
      ...seriesDefaults
    }
  }
}