<template>
  <svg
    xmlns="http://www.w3.org/2000/svg"
    xmlns:xlink="http://www.w3.org/1999/xlink"
    version="1.1"
    :width="(rectangles.length + 1) * settings.rectWidth"
    :height="30"
    class="pcp-line"
  >
    <g v-for="(rect, index) in rectangles" :key="index">
      <rect
        v-if="!isNaN(pcd.means[index])"
        :x="rect.x"
        :y="rect.y"
        :width="rect.width"
        :height="rect.height"
        :fill="rect.fill"
        v-b-tooltip.hover.top.nofade.d0.dh0.html="descriptions[index]"
      />
    </g>
  </svg>
</template>

<style lang="scss" scoped>
</style>

<script>
import * as Coloring from "@/lib/Coloring.js";

export default {
  name: "RectangleChart",
  props: ["pcd", "settings"],
  computed: {
    rectangles() {
      if (this.settings.dpsMatrixType == "1") return this.rectangles1;
      else if (this.settings.dpsMatrixType == "2") return this.rectangles2;
      else return this.rectangles3;
    },
    rectangles1() {
      return [...this.pcd.means.keys()].map(i => {
        const width = this.pcd.significances[i] * this.settings.rectWidth;
        const height = this.pcd.significances[i] * this.settings.rectHeight;

        return {
          x:
            i * this.settings.rectWidth + (this.settings.rectWidth - width) / 2 + (this.pcd.positions == "between" ? 0.5 * this.settings.rectWidth : 0),
          y: (this.settings.rectHeight - height) / 2,
          fill: Coloring.colorOf(this.pcd.normMeans[i]),
          width: width,
          height: height
        };
      });
    },
    rectangles2() {
      return [...this.pcd.means.keys()].map(i => {
        var width = this.settings.rectWidth;
        var height = this.settings.rectHeight;

        return {
          x: i * this.settings.rectWidth + (this.pcd.positions == "between" ? 0.5 * this.settings.rectWidth : 0),
          y: 0,
          fill: Coloring.colorOf(this.pcd.normMeans[i]),
          width: width,
          height: height
        };
      });
    },
    rectangles3() {
      return [...this.pcd.means.keys()].map(i => {
        var width = this.settings.rectWidth;
        var height = this.settings.rectHeight;

        return {
          x: i * this.settings.rectWidth + (this.pcd.positions == "between" ? 0.5 * this.settings.rectWidth : 0),
          y: 0,
          fill: Coloring.colorOf(
            this.pcd.significances[i],
            [255, 255, 255],
            [0, 255, 0]
          ),
          width: width,
          height: height
        };
      });
    },
    descriptions() {
      return [...this.pcd.means.keys()].map(i => {
        const position =
          this.pcd.positions == "between" ? `${i + 1}-${i + 2}` : i + 1;
        return `
          ${this.pcd.pcdInfo.description} [${position}] <br>
          mean: ${this.pcd.means[i].toFixed(4)} <br>
          deviation: ${this.pcd.deviations[i].toFixed(4)} <br>
          norm-mean: ${this.pcd.normMeans[i].toFixed(4)} <br>
          norm-deviation: ${this.pcd.normDeviations[i].toFixed(4)} <br>
          significance: ${this.pcd.significances[i].toFixed(4)}
          `;
      });
    }
  }
};
</script>