  <div id="activity-bar">
    <button
      v-on:click="set(description)"
      v-b-tooltip.hover.left.nofade.d0.dh0="'description'"
      v-bind:class="{ active: value == description }"
    >
      <font-awesome-icon icon="file-alt" />
    </button>
    <button
      v-on:click="set(settings)"
      v-b-tooltip.hover.left.nofade.d0.dh0="'settings'"
      v-bind:class="{ active: value == settings }"
    >
      <font-awesome-icon icon="cogs" />
    </button>
