<style lang="scss" scoped>
#activity-bar {
  width: 55px;
  max-width: 55px;
  min-width: 55px;
  height: 100%;
  background: #24292e;
}

#activity-bar > button {
  background: none;
  border: none;
  width: 100%;
  height: 55px;
  border-right: solid 2px #24292e;
  color: #74777b;
  font-size: 22px;
  cursor: pointer;
  margin: 0;
  padding: 0 0 0 2px;
  outline: none !important;
}

#activity-bar > button:hover,
#activity-bar > button.active {
  color: #fafbfc;
  border-right: solid 2px #74777b;
}
</style>

<script>
import Description from '@/components/activity_panels/Description.vue'
import Settings from '@/components/activity_panels/Settings.vue'
import Json from '@/components/activity_panels/Json.vue'

export default {
  name: "ActivityBar",
  props: [ "value" ],
  data() {
    return {
      description: Description,
      json: Json,
      settings: Settings
    }
  },
  methods: {
    set(activity) {
      if (activity == this.value) {
        this.$emit('input', null)
      } else {
        this.$emit('input', activity)
      }
    }
  }
};
</script>