import Vue from 'vue'
import App from './App.vue'
import router from './router'

// FontAwesome
import { library } from '@fortawesome/fontawesome-svg-core'
import { faFileAlt as fasFileAlt } from '@fortawesome/free-regular-svg-icons'
import { faLifeRing, faFileAlt as farFileAlt, faDownload, faCogs, faCode, faDna } from '@fortawesome/free-solid-svg-icons'
import { FontAwesomeIcon } from '@fortawesome/vue-fontawesome'

library.add(fasFileAlt, farFileAlt, faCogs, faLifeRing, faCogs, faDownload, faCode, faDna)
Vue.component('font-awesome-icon', FontAwesomeIcon)
 
// Bootstrap
import BootstrapVue from 'bootstrap-vue'
Vue.use(BootstrapVue)

// Highcharts
import HighchartsVue from 'highcharts-vue'
import Highcharts from 'highcharts'
import stockInit from 'highcharts/modules/stock'
import HighchartsMore from'highcharts/highcharts-more'
HighchartsMore(Highcharts)
stockInit(Highcharts)
Vue.use(HighchartsVue)

Vue.config.productionTip = false

new Vue({
  router,
  render: h => h(App)
}).$mount('#app')

