const colorMinus = [0, 0, 255]
// const colorZero = [235, 235, 235]
const colorPlus = [255, 0, 0]

export function colorOf(value, colorStart = colorMinus, colorEnd = colorPlus) {
  // if (value < 0) {
  //   color = this.interpolateColor(colorMinus, colorZero, 1 + value)
  // } else {
  //   color = this.interpolateColor(colorZero, colorPlus, value)
  // }

  const color = interpolateColor(colorStart, colorEnd, (value + 1) / 2)
  return "rgb(" + color.join(",") + ")"
}

// gamma correct color interpolation
function interpolateColor(color1, color2, factor) {
  // rounding problems
  if (factor < 0)
    factor = 0;
  else if (factor > 1)
    factor = 1;

  let result = color1.slice();

  for (let i = 0; i < 3; i++) {
    let c = interpolate(Math.pow(color1[i] / 255, 2.2), Math.pow(color2[i] / 255, 2.2), factor);
    result[i] = Math.floor(Math.pow(c, 1 / 2.2) * 255);
  }

  return result;
}

// linear interpolation
function interpolate(v1, v2, factor) {
  return v1 + factor * (v2 - v1);
}
