<template>
  <b-container>
    <b-form @submit.prevent="onSubmit">
      <b-form-group label="jaspar matrix id">
        <b-form-input id="ident" v-model="ident" placeholder trim></b-form-input>
      </b-form-group>

      <b-form-checkbox v-model="hasPCDs">has PCD data (calcialted from .sites files)</b-form-checkbox>

      <b-button type="submit" variant="outline-primary">Search</b-button>
    </b-form>

    <table class="table table-hover">
      <tbody>
        <tr v-for="line of results" v-bind:key="line.ident">
          <td>{{ line.ident }}</td>
          <td>
            <b-button :to="{name: 'Motif', params: {ident: line.ident}}">open</b-button>
          </td>
        </tr>
      </tbody>
    </table>
  </b-container>
</template>

<style lang="scss" scoped>
</style>

<script>
function load(vm) {
  let params = [];

  if (vm.ident != null) params.push(`ident=${vm.ident}`);
  params.push(`hasPCDs=${vm.hasPCDs}`);

  fetch(`http://localhost:8081/api/motif-dps/list?${params.join("&")}`)
    .then(function(response) {
      return response.json();
    })
    .then(function(data) {
      vm.results = data.results;
    });
}

export default {
  name: "Search",
  data() {
    return {
      ident: null,
      hasPCDs: true,
      results: []
    };
  },
  created() {
    load(this)
  },
  methods: {
    onSubmit() {
      load(this);
    }
  }
};
</script>

