<template>
  <div class="motif-view">
    <header>
      <h1>
        <font-awesome-icon icon="dna" />
        {{ model.metadata.matrix_id }}
      </h1>
    </header>

    <img :src="model.metadata.sequence_logo" width="500x" />

    <div v-for="dataLine in selectedDataLines" :key="dataLine.ident">
      <highcharts :options="dataLine.chartOptions" :updateArgs="[true, true, false]" v-show="settings.showCharts" />
      <rectangle-chart :pcd="dataLine" :settings="settings" v-show="settings.showRects" />

      <!-- {{ pcp.significanceSum }} -->
    </div>
  </div>
</template>

<style lang="scss" scoped>
#motif-viewer {
  padding-left: 50px;
}
</style>

<script>
import { addChartOptions } from "@/lib/PCDCharts.js";
import RectangleChart from "@/components/RectangleChart.vue";

export default {
  name: "MotifView",
  props: ["model", "settings"],
  components: { RectangleChart },
  computed: {
    dataLines() {
      return Object.entries(this.model.pcds).map(entry =>
        addChartOptions(entry[1], this.settings)
      );
    },
    selectedDataLines() {
      return this.dataLines.filter(d =>
        this.settings.selectedPCDs.some(str => str == d.ident)
      ).sort((a, b) => a.ordering[this.settings.pcdOrder] - b.ordering[this.settings.pcdOrder]);
    }
  }
};
</script>
