<template>
  <div class="activity-panel">
    <h1>
      <font-awesome-icon :icon="['fas', 'cogs']" />view description
    </h1>
    <h2>Charts</h2>

    <form class="section">
      <div class="form-group">
        <!-- chart -->

        <b-form-checkbox v-model="settings.showCharts">display</b-form-checkbox>
        <b-form-group label="chart options">
          <b-form-checkbox-group
            v-model="settings.selectedCharts"
            :options="seriesOptions"
            name="series-checkboxes"
            stacked
          ></b-form-checkbox-group>
        </b-form-group>

        <b-form-group label="chart height">
          <b-form-input v-model.number="settings.chartHeight" type="number"></b-form-input>
        </b-form-group>

        <b-form-group label="presets">
          <b-button v-on:click="settings.selectedCharts = ['curvedMeanLine', 'meanErrorbar']">1</b-button>
          <b-button v-on:click="settings.selectedCharts = ['meanLine', 'arearange']">2</b-button>
          <b-button v-on:click="settings.selectedCharts = ['columnrange']">3</b-button>
        </b-form-group>
      </div>
    </form>

    <!-- rectangles -->

    <h2>heatmap like rectangles</h2>

    <form class="section">
      <b-form-checkbox v-model="settings.showRects">display</b-form-checkbox>

      <div class="form-group">
        <legend class="col-form-label">PCD matrix variant</legend>
        <div class="form-check">
          <input
            class="form-check-input"
            type="radio"
            id="dps-matrix-1"
            value="1"
            v-model="settings.dpsMatrixType"
          />
          <label for="dps-matrix-1">color by value, size by significance</label>
        </div>
        <div class="form-check">
          <input
            class="form-check-input"
            type="radio"
            id="dps-matrix-2"
            value="2"
            v-model="settings.dpsMatrixType"
          />
          <label for="dps-matrix-2">color by value</label>
        </div>
        <div class="form-check">
          <input
            class="form-check-input"
            type="radio"
            id="dps-matrix-3"
            value="3"
            v-model="settings.dpsMatrixType"
          />
          <label for="dps-matrix-3">color by significance</label>
        </div>
      </div>
    </form>

    <h2>PCD</h2>
    <form class="section">
      <div class="form-group">
        <label for="dps-matrix-sort">Order by</label>
        <select class="form-control" id="dps-matrix-sort" v-model="settings.pcdOrder">
          <option selected value="significanceSum">cumulative significance</option>
          <option value="significanceMax">maximum significance</option>
          <option value="order">PCP order</option>
        </select>
      </div>
    </form>
    <!-- show / hide PCDs -->
    <form class="section">
      <b-form-group label="chart options">
        <b-form-checkbox-group
          v-model="settings.selectedPCDs"
          :options="pcdOptions"
          name="series-checkboxes"
          stacked
        ></b-form-checkbox-group>
      </b-form-group>
    </form>

    <h2>FGDR</h2>
    <h2>Sequence logo</h2>
    <h2>Highlighting</h2>
  </div>
</template>

<script>
import { series } from "@/lib/PCDCharts.js";

export default {
  props: ["model", "settings"],
  data: function() {
    return {
      seriesOptions: Object.keys(series).map(key => {
        return { text: series[key].name, value: key };
      })
    };
  },
  computed: {
    pcdOptions() {
      return Object.keys(this.model.pcds).map(key => {
        return { text: this.model.pcds[key].pcdInfo.description, value: key };
      });
    }
  }
};
</script>

