export function processTable(table) {
  for (let entry of Object.entries(table)) {
    let pcd = entry[1]
    pcd.ident = entry[0]
    pcd.means = convertNaNs(pcd.means)
    pcd.deviations = convertNaNs(pcd.deviations)
    pcd = addNormalized(pcd)
    pcd = addSignificances(pcd)
    pcd = addOrdering(pcd)
  }

  return table
}

function addNormalized(pcd) {
  var interval = pcd.pcdInfo.max - pcd.pcdInfo.min
  var halfInterval = interval / 2
  var middle = pcd.pcdInfo.min + interval / 2

  pcd.normMeans = pcd.means.map(m => (m - middle) / halfInterval)
  pcd.normDeviations = pcd.deviations.map(d => d / halfInterval)

  return pcd
}

function addSignificances(pcd) {
  pcd.significances = pcd.normDeviations.map(d => (1 - d) * (1 - d))

  return pcd
}

function addOrdering(pcd) {
  pcd.ordering = {
    significanceSum: pcd.significances.filter(d => !isNaN(d)).reduce((a, b) => a + b, 0),
    significanceMax: Math.max(...pcd.significances.filter(d => !isNaN(d))),
    order: pcd.significances.filter(d => !isNaN(d)).reduce((a, b) => a + b, 0)
  }

  return pcd
}

function convertNaNs(arr) {
  return arr.map(value => {
    if (value == "NaN")
      return NaN
    else
      return value
  })
}